<template>
  <div id="app">
    <header>
      <NavBar/>
    </header>
    <main>
      <router-view></router-view>
    </main>
    <footer>
      <SponsorSection v-if="showSponsorFooter"/>
      <TheFooter/>
    </footer>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import { NavBar, TheFooter, SponsorSection } from '@/views'

export default {
  name: 'app',
  data () {
    return {}
  },
  computed: {
    ...mapState(['lang']),
    showSponsorFooter () {
      return this.$route.path !== '/sponsors'
    }
  },
  methods: mapMutations(['toggleLang']),
  components: { NavBar, TheFooter, SponsorSection }
}
</script>

<style lang="scss">
@import './styles/global';
#app {
  background: linear-gradient($dark-gray, $light-gray, $light-gray);
}

.mb-50 {
  margin-bottom: 50px;
}
</style>



